.Button{
    text-align:center; 
    margin: 10px 0px;
}

header{
    background-color: #D9B6A3;
}

.primary{
    background-color: #D9B6A3;
    width: 100%;
    height: 100%;
    padding-top:0px;
    
    padding-bottom: 0px;
}

.aboutMe{
    background-color: #808C89;
    width: 100%;
}

@media (max-width: 992px) {
  .second{
    height: 700px;
  }

  .namePhoto{
      width: 80%;
  }

  .primaryPhoto{

    bottom: 0%;
  }

  .primary-div{
      text-align: center;
  }

  .first-col{
    max-height: 300px !important;
  }
}

.first-col{
    height: 600px;
    max-height: 100vh;
}


.aboutMe-content{
    z-index: 5;
    margin: 10px 0px;
}

.expe, .works{
    background-color: #BABFBB;
    width: 100%;
    
}

.expeDescrip{
    font-size: small;
    width: 70%;
}

.namePhoto{
    position: absolute;
    bottom: 120px;
    left: 60px;
    
}

.secondPhoto-div{
    /* text-align: center; */
    height: 100% !important;
}

.second-col{
    height: 350px;
}

.secondPhotoDeco{
    position: absolute;
    width: 350px;
    height: 280px;
    max-height: 260px;
    z-index: 1;
    background-color: #FFFFFF;
    transform:rotate(5deg);
    box-shadow: 10px 10px  #707070;
}

.secondPhoto{
    height: 240px;
    z-index: 2;
    position: absolute;


}

