    padding-top: 5%;
} */

.expDeco{
    width: 4px;
    height: 160px;
    position: relative;
    left: 100%;
    background-color: #141C26;
}

.expCol{
    padding-top: 15px;
    padding-bottom: 15px;
}

.expeTitle{
    text-align: center;
    color: white;
}

.skills{
    background-color: #141C26;
    width: 100%;
    height: 100vh;
}

.skillTitle {
    color: #FFFFFF;
    text-align: center;
    margin-bottom: 10px;
    box-sizing: border-box;
}

.buttonLink{
    width: 90%;
    height: auto;
	/* height: auto;
	max-width: 100%;
	max-height: 100%; */

}

.imgContainer{
    height:130px;
    width: 200px;
    overflow:hidden;
    margin-bottom: 10px;
}

.progress{
      
    width:200px;
    
}

.skillName{
    color: #FFFFFF;
}

.project-link{
    margin: 10px;
}

.work-content{
    width: 100%;
}

h4{
    color: #141C26;
}

.link{
    margin:  0px 10px;
} 

.imgContainer :hover{
    box-shadow: 3px 3px 15px gray;
    
}